<!--  -->
<!-- eslint-disable vue/no-v-html -->

<template>
  <div
    v-if="show"
    ref="componentContainer"
    class="cert-poster"
  >
    <img
      class="cert-poster__background"
      :src="certBackground"
    >
    <div class="cert-poster__title">
      {{ certTitle }}
    </div>
    <div
      v-if="showCertGreet"
      class="cert-poster__greet"
    >
      {{ certGreet }}
    </div>
    <div
      class="cert-poster__content"
      v-html="certContent"
    />
    <div class="cert-poster__no">
      {{ certNumber }}
    </div>
    <div
      class="cert-poster__signature"
      v-html="certSignature"
    />
    <img
      class="cert-poster__stamp"
      :src="certStamp"
    >
  </div>
</template>

<script>
import html2canvas from "html2canvas";

export default {
  name: "CertPoster",

  props: {
    value: {
      type: Boolean,
      default: false
    },

    // 
    certTitle: {
      type: String,
      default: ""
    },

    // 
    certBackground: {
      type: String,
      default: ""
    },

    // 
    certStamp: {
      type: String,
      default: ""
    },

    // 
    certGreet: {
      type: String,
      default: ""
    },

    // 
    certContent: {
      type: String,
      default: ""
    },

    // 
    certSignature: {
      type: String,
      default: ""
    },

    // 
    certNumber: {
      type: String,
      default: ""
    },

    // 
    showCertGreet: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      show: this.value
    };
  },

  watch: {
    value(newVal) {
      this.show = newVal;

      if (newVal) {
        this.$nextTick(() => {
          this.createPoster()
        });
      }
    },

    certContent(newVal) {
      this.certContent = this.format(newVal);
    },

    certSignature(newVal) {
      this.certSignature = this.format(newVal);
    }
  },

  methods: {
    // 
    format(str) {
      const list = [
        ()=> str.replace(/\n|/g, "<br>"), //  HTML 
        ()=> str.replace(/\s/g, "&nbsp;") //  &nbsp;
      ]

      list.forEach(elm => {
        str = elm()
      })

      return str
    },

    // 
    createPoster() {
      //  http://html2canvas.hertzen.com/configuration/
      const opts = {
        scale: 3, // 
        // logging: true, //  html2canvas 
        useCORS: true, // 
        allowTaint: true, // 
        backgroundColor: "#0000", // 
        scrollY: 0 //  0 bughttps://github.com/niklasvh/html2canvas/issues/1878
      };

      // HTML 
      html2canvas(this.$refs.componentContainer, opts)
        .then(canvas => {
          const base64 = canvas.toDataURL("image/jpeg"); //  base64 

          this.$emit("success", base64);
          this.close();
        })
        .catch(() => {
          this.$emit("fail");
          this.close();
        });
    },

    close() {
      this.$emit("input", false);
    }
  }
};
</script>

<style>
.cert-poster {
  position: fixed;
  top: -999999px;
  left: -999999px;
  width: 600px;
  height: 460px;
  padding: 104px 60px 38px;
  color: #444244;
  font-size: 12px;
  font-weight: 400;
  box-sizing: border-box;
  /*  Ant Design */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "PingFang SC",
    "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.cert-poster .cert-poster__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.cert-poster .cert-poster__title {
  padding-bottom: 20px;
  font-size: 20px;
  font-weight: 800;
  text-align: center;
}

.cert-poster .cert-poster__greet {
  font-size: 16px;
  color: #b48d2c;
  font-weight: 800;
  text-align: center;
  padding-bottom: 10px;
}

.cert-poster .cert-poster__content {
  line-height: 20px;
  text-indent: 24px;
}

.cert-poster .cert-poster__no {
  position: absolute;
  bottom: 38px;
  left: 60px;
}

.cert-poster .cert-poster__signature {
  position: absolute;
  right: 60px;
  bottom: 38px;
  width: 220px;
  line-height: 20px;
  text-align: right;
}

.cert-poster .cert-poster__stamp {
  position: absolute;
  right: 60px;
  bottom: 29px;
  width: 88px;
  height: 88px;
}
</style>
